.info-section {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: url(../assets/images/textures/t2.jpg) no-repeat center
    center/cover;
}

.info-section .introduction {
  z-index: 2;
  width: 70%;
  height: 60%;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  background: none;
  border: none;
  border-radius: 1rem;
  box-shadow: none;
}

.introduction .avatar {
  background: url("../assets/images/Developer.jpg") no-repeat center
    center/cover;
  height: 100%;
  width: 40%;
  border-radius: 50%;
}

.introduction .greetings {
  color: #fff;
  font-size: 1.5rem;
  width: 50%;
  height: 100%;
  padding: 0.3rem 1rem;
  text-shadow: 4px 4px 2px rgb(107, 84, 239);
  display: flex;
  align-items: center;
  justify-content: center;
}
